@media screen and (max-width: 600px) {
  .intro {
    display: flex;
    flex-direction: column;
    margin: 4em;
    padding-right: 0em;
    margin-top: 10em;
  }

  .title_text {
    font-size: 200%;
  }
}


@media screen and (min-width: 600px) {
  .intro {
    display: grid;
    grid-template-columns: 1fr 3fr 2fr;
    gap: 10px;
    padding-top: 1%;
  }

  .title_text {
    font-size: 300%;
  }
}



.title_text {
  display: inline-flex;
  font-family: 'lora';
  grid-column: 2;
  padding-top: 25%;
}

.intro_card {
  grid-column: 2;
}


.intro_text {
  display: inline-flex;
  font-family: 'lora';
  font-size: 200%;
}

.email {
  font-family: 'lora-italic';
}

.scroll_prompt {
  font-family: 'lora-italic';
  position: fixed; /* Fixed position to stay at the bottom */
  bottom: 20px; /* Distance from the bottom */
  left: 0;
  right: 0;
  text-align: center; /* Center the content horizontally */
  z-index: 1000; /* Ensure it's above other content */
}

.scroll_prompt p {
  font-size: 150%;
  margin: 0; 
  padding: 0; 
}

.scroll_prompt span {
  font-family: 'lora';
  margin: 10px auto 0; /* Margin to space out the arrow from the text */
  font-size: 150%;
}

